// Componentes
import { SubTitle } from './components/elements/SubTitle';
import { Title } from './components/elements/Title';

const { urlServer, requestOptionsGet } = resourceServer;

const getlist = async (setPatientes) => {

     const resp = await fetch(`${urlServer}Patient`, addToken(requestOptionsGet))
     //const resp = await fetch(`${urlServer}Patient`, requestOptionsGet )
     let { entry, total, link } = await resp.json();

     // Más de una pagina de respuesta
     if (link.length > 1) {
          const resp = await fetch(`${urlServer}Patient?_count=${total}`, addToken(requestOptionsGet))
          const json = await resp.json();
          entry = json.entry;
     };

     console.log( entry.filter(entry => entry.resource?.identifier && entry.resource?.address).map( entry => entry.resource ) );

     // Carga previa de los pacientes
     setPatientes( entry.filter(entry => entry.resource?.identifier && entry.resource?.address).map(({ resource }) => ({
               id: resource.id,
               nombre: resource.name[0].text,
               cnt: resource.identifier[0].value,
               ala: resource.address ? resource.address[0].district : 'na',
               sala: resource.address ? resource.address[0].state : 'na',
               cama: resource.address ? resource.address[0].text : 'na',
               state: 0
          })));
};

