const getUnits = async (setUnits) => {

     const resp = await fetch(`${urlServer}Location`, addToken(requestOptionsGet))
     let { entry, total } = await resp.json();

     // Más de 10 paginas de respuesta
     if (total > 10) {
          const resp = await fetch(`${urlServer}Location?_count=${total}`, addToken(requestOptionsGet))
          const json = await resp.json();
          entry = json.entry;
     };

     console.log(entry.map( entry => entry.resource ));

     const arrayUnit = entry.map(entry => ({
          ala: entry.resource.name,
          salas: [
               {
                    sala: entry.resource.identifier[1].value,
                    capacidad: entry.resource.address.text
               }
          ]
     }))


     setUnits(arrayUnit);
};


const Home = () => {

     // Color de fondo:
     document.getElementById("root").style.backgroundColor = '#d1e1e5';

     const dispatch = useDispatch();
     dispatch(assignId('0'));

     const classes = Stl();
     const { _lg } = MediaQuery();

     const [pacientes, setPatientes] = useState([]);
     // Distribución física y capacidad de las instalaciones
     const [units, setUnits] = useState(
          [
               {
                    ala: '',
                    salas: [
                         { sala: '', capacidad: 0 }
                    ]
               }
          ]
     );

     const [buttonCreate, setButtonCreate] = useState(false);

     // liberar alguna instancia de neuroDesk empleada
     const instancia = useSelector((state) => state.neuroDesk.value);

     const liberarIntanciaNeuroDesk = () => {
          if (instancia === null) return;
          dispatch(clearIntance({ ...instancia, available: true }));
     };

     liberarIntanciaNeuroDesk();

     useEffect(() => {

          // console.clear();
          localStorage.removeItem('Data');
          localStorage.removeItem('idPatient');
          localStorage.removeItem('cntPatient');
          localStorage.removeItem('intervalos');
          localStorage.removeItem('units');
          localStorage.removeItem('patients')

          const getdata = async () => {
               getUnits(setUnits);
               await getlist(setPatientes);
               // findAlerts( setPatientes );
               setButtonCreate(true);
          }

          getdata();  
     }, []);

     console.log( "pacientes: ",pacientes );


     const handleId = (_id) => {
          console.log("home => idPatient: ", _id);
          localStorage.setItem('idPatient', JSON.stringify({ id: _id }, null, 2));
     };

     const handleEnfermeria = (cntPatient) => {

          localStorage.setItem('cntPatient', JSON.stringify(cntPatient, null, 2));
          localStorage.setItem('patients', JSON.stringify(pacientes))

          getFromServer(
               JSON.parse(localStorage.getItem('cntPatient')),
               new Date().toLocaleString("en-GB").split(', ')[0].split('/').reverse().join('-') //"2022-12-19"
          ).then(({ data, idPatient }) => {

               data.Fecha = new Date().toLocaleString("en-GB").split(', ')[0].split('/').reverse().join('-') //"2022-12-19"  ;
               localStorage.setItem('Data', JSON.stringify(data, null, 2));
               localStorage.setItem('idPatient', JSON.stringify(idPatient, null, 2));
               console.log("home => idPatient: ", idPatient);

               // const  baseUrl = window.location.href.split('/coc/')[0]
               // window.location.href = `${baseUrl}/coc/panel/uci/formularioEnfermeria` ;

          });

     };

     const bg_color = [
          '#60AE53', // verde
          '#F6E05E', // amarillo
          '#F06F11', // naranja
          '#D23531' // rojo
     ]

     const TitleSpan = (props) => {

          return (
               <Box
                    className={classes.bg_blue} my={1} pl={2}
               >
                    <Grid
                         container
                         direction="row"
                         justifyContent="space-between"
                         alignItems="center"
                         spacing={1}
                         columns={20}
                    >
                         <Grid
                              item xs={17}
                         >
                              <Title color={'white'} span={props.span}>
                                   {props.text}
                              </Title>
                         </Grid>

                         <Grid
                              item xs={3}
                         >
                              {props.children}
                         </Grid>
                    </Grid>


               </Box>
          )
     };

     const subTitle = (array, num_ala, let_sala, cap) => {
          // Camas disponibles por sala
          const disp = cap - array.length;

          return (
               <Box className={[classes.text_bold, classes.text_color_title, classes.bg_white].join(' ')} mb={1}>
                    <Grid
                         container
                         direction="row"
                         justifyContent="space-between"
                         alignItems="center"
                         columns={15}
                    >
                         <Grid
                              xs={11}
                              item
                              py={1}
                         >
                              <Box pl={1}>
                                   <SubTitle >
                                        Ala : {num_ala} - Sala : {let_sala}
                                   </SubTitle>
                              </Box>
                         </Grid>
                         <Grid
                              xs={4}
                              item
                              py={1}
                         >
                              <Box pl={1}>
                                   <SubTitle >
                                        Disponibilidad: {disp}/{cap}
                                   </SubTitle>
                              </Box>
                         </Grid>

                    </Grid>
               </Box>
          );
     };

     const Element = ({ obj, asig }) => {

          return (
               <Box style={{ paddingBottom: '2px' }}>

                    <Grid
                         container
                         direction="row"
                         justifyContent="space-between"
                         alignItems="center"
                         columns={12}
                    >

                         <Grid
                              xs={9}
                              item
                         >
                              <Box
                                   px={1} mr={1} py={0.25} my={0.2}
                                   style={{ backgroundColor: bg_color[obj.state], fontSize: _lg ? '25px' : '15px', fontWeight: '500' }}
                                   className={classes.font_monserrat}
                              >
                                   {asig ?
                                        <Link to="/coc/panel/uci/monitor" style={{ color: 'rgb(42, 20, 55)', textDecorationLine: 'none' }}>
                                             <span className={classes.button} onClick={() => handleId(obj.id)}>
                                                  Cama {obj.cama} : {obj.nombre}
                                             </span>
                                        </Link>
                                        :
                                        <span>
                                             Cama {obj.cama} : {obj.nombre}
                                        </span>
                                   }
                              </Box>
                         </Grid>

                         <Grid
                              xs={3}
                              item
                         >
                              <Box
                                   px={1} py={0.25} my={0.2}
                                   style={{ backgroundColor: 'rgb(255, 255, 255)', fontSize: _lg ? '25px' : '15px', fontWeight: '500' }}
                                   className={[classes.text_color_title, classes.text_center, classes.font_monserrat].join(' ')}
                              >
                                   <Link to="/coc/panel/uci/formularioEnfermeria" style={{ color: 'rgb(42, 20, 55)', textDecorationLine: 'none' }}>
                                        <span className={classes.button} onClick={() => handleEnfermeria(obj.cnt)}>
                                             Formulario
                                        </span>
                                   </Link>


                              </Box>
                         </Grid>
                         {
                              // <Grid
                              //      xs={2}
                              //      item
                              // >
                              //      <Box style={{backgroundColor:bg_color[3], fontSize:_xl?'18px':'13px'}} className={[classes.text_white, classes.text_center, classes.font_monserrat].join(' ')} >
                              //           BUN {obj.BUN}
                              //      </Box>
                              // </Grid>
                         }
                    </Grid>
               </Box>
          );
     };

     const cell = (array_alas_salas, array_pacientes) => {

          return (
               array_alas_salas.map((elem, indx_0) => {

                    let array_ala = array_pacientes.filter(filter_elem => filter_elem.ala === elem.ala)

                    return (

                         elem.salas.map((sala_elem, indx_1) => {

                              let array_sala = array_ala.filter(filter_elem_sala => filter_elem_sala.sala === sala_elem.sala);

                              return (
                                   <Grid
                                        item
                                        xs={6}
                                        key={indx_1}
                                   >
                                        {
                                             subTitle(array_sala, elem.ala, sala_elem.sala, sala_elem.capacidad)
                                        }
                                        {
                                             array_sala.map((elem_sala, indx_2) => {
                                                  const flag = true;
                                                  return (
                                                       <Element key={indx_2} obj={elem_sala} asig={flag} />
                                                  )
                                             })
                                        }
                                   </Grid>
                              );
                         })

                    );

               })

          );
     };

     const ButtonCreatePatient = () => {

          const ButtonPage = styled('button')({
               width: '90%',
               fontFamily: 'Montserrat',
               paddingBlock: '0.5rem',
               paddingInline: '0.8rem',
               background: 'white',
               borderRadius: '2px',
               fontWeight: 'bold',
               margin: '0.2rem',
               color: '#0d47a1',
               border: '0',
               '&:hover': {
                    background: '#0d47a1',
                    color: 'white',
                    cursor: 'pointer'
               },
          });

          if (buttonCreate) {

               return (
                    <Link
                         to="/coc/panel/uci/crearPaciente"
                         style={{ color: 'rgb(42, 20, 55)', textDecorationLine: 'none', width: '100%' }}
                    >
                         <ButtonPage onClick={() => {
                              localStorage.setItem('units', JSON.stringify(units));
                              localStorage.setItem('patients', JSON.stringify(pacientes))
                         }}>
                              Crear paciente
                         </ButtonPage>
                    </Link>
               )
          }

          return <p>''</p>;
     }

     const sin_asignar = pacientes.filter(elm => elm.ala === '-');

     return (
          <>
               <TitleSpan text={'Inicio'} span >
                    <ButtonCreatePatient />
               </TitleSpan>



               <Grid
                    container
                    direction="row"
                    justifyContent="space-between"
                    alignItems="flex-start"
                    spacing={1}
                    columns={12}
               >
                    {cell(units, pacientes)}

               </Grid>

               <TitleSpan text='Pacientes sin asignar' />

               <Grid
                    container
                    direction="row"
                    justifyContent="space-between"
                    alignItems="flex-start"
                    spacing={1}
                    columns={12}
               >
                    {sin_asignar.map((elm, indx) => {
                         return (
                              <Grid
                                   item
                                   xs={6}
                                   key={indx}
                              >
                                   <Element obj={elm} asig={false} />
                              </Grid>
                         )
                    })}

               </Grid>
          </>
     );
};

export default Home;
